import React, { Component } from 'react';
import AdminLayout from '../../layouts/AdminLayout';
import { connect } from 'react-redux';
import { productActions } from '../../store/actions/product.action';
import CardProductHorizontal from './cardProductHorizontal';
import LoadinCircle from '../../components/Progress/loadingCircle';
import { Link, withRouter } from 'react-router-dom';

import {
	Grid,
	Card,
	Typography,
	CardContent,
	DialogContent,
	DialogActions,
	Button,
	Dialog,
	DialogTitle
} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import { brandActions } from '../../store/actions/brand.action';
import FormDialog from './modalForm';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import CloseIcon from '@material-ui/icons/Close';
import EditIcon from '@material-ui/icons/Edit';
import { userActions } from '../../auth/store/actions/user.actions';
import AuthRegister from '../auth/registerPage';
import UserCard from './userCard';

// import Pagination from '@material-ui/lab/Pagination';

function MyProfile(props) {
	const [profileState, setProfileState] = React.useState({});

	React.useEffect(() => {
		switch (profileState.selectedOption) {
			case 'profile':
				break;
			case 'products':
				if (props.userLogin.role == 'admin') {
					props.handleListProduct('admin');
				} else {
					props.handleListProduct();
				}
				break;
			case 'brands':
				props.handleListBrand();
				break;
			case 'users':
				props.handleListUsers();
				break;
			default:
				if (props.userLogin.role == 'admin') {
					props.handleListProduct('admin');
				} else {
					props.handleListProduct();
				}
				break;
		}
	}, [profileState.selectedOption]);

	const EditElement = id => {
		props.history.push({ pathname: '/product/edit', state: { id } });
	};

	const renderMain = () => {
		switch (profileState.selectedOption) {
			case 'profile':
				return <AdminProfile />;
			case 'products':
				return (
					<AdminProductList
						dataProps={props}
						handleEditElement={id => EditElement(id)}
						handleDeleteElement={id => props.handleDeleteProduct(id)}
					/>
				);
			case 'brands':
				return (
					<AdminBrandList
						dataProps={props}
						handleAddBrand={brand =>
							props.handleCreateBrand({ name: brand.name, models: brand.models })
						}
						handleEditBrand={brand => props.handleEditBrand(brand)}
						handleDeleteBrand={id => props.handleDeleteBrand(id)}
					/>
				);
			case 'users':
				return (
					<AdminUserList
						dataProps={props}
						handleListUsers={() => props.handleListUsers()}
						handleAddUser={(user, toEdit) => props.handleAddUser(user, toEdit)}
						handleDeleteUser={id => props.handleDeleteUser(id)}
					/>
				);
			default:
				return (
					<AdminProductList
						dataProps={props}
						handleEditElement={id => EditElement(id)}
						handleDeleteElement={id => props.handleDeleteProduct(id)}
					/>
				);
		}
	};

	return (
		<AdminLayout
			updateState={adminState => {
				setProfileState({ selectedOption: adminState });
			}}
		>
			{renderMain()}
		</AdminLayout>
	);
}

const mapStateToProps = ({ productReducer, brandReducer, authReducers }) => {
	return {
		loadingList: productReducer.loadingList,
		dataList: productReducer.dataList,

		loadingListBrand: brandReducer.loadingList,
		listBrand: brandReducer.listBrand,

		loadingListUser: authReducers.loadingListUser,
		listUser: authReducers.listUser,

		loadingRegister: authReducers.loadingRegister,
		userRegister: authReducers.userRegister,
		userLogin: authReducers.userLogin
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleListProduct: role => {
			if (role) {
				dispatch(productActions.ListProduct(undefined, 'loadbrand'));
			} else {
				dispatch(productActions.ListProduct('mycustom'));
			}
		},
		handleDeleteProduct: id => {
			dispatch(productActions.DeleteProduct(id));
		},

		handleListUsers: () => {
			dispatch(userActions.ListUsers());
		},
		handleAddUser: (data, toEdit) => {
			if (toEdit) {
				dispatch(userActions.UpdateUser(data));
			} else {
				dispatch(userActions.RegisterUser(data));
			}
		},
		handleDeleteUser: dataID => {
			dispatch(userActions.DeleteUser(dataID));
		},

		handleListBrand: () => {
			dispatch(brandActions.ListBrand());
		},
		handleCreateBrand: brand => {
			dispatch(brandActions.CreateBrand(brand));
		},
		handleEditBrand: brand => {
			dispatch(brandActions.UpdateBrand(brand));
		},
		handleDeleteBrand: id => {
			dispatch(brandActions.DeleteBrand(id));
		}
	};
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MyProfile));

function AdminProductList(props) {
	const { loadingList, dataList } = props.dataProps;

	return (
		<Grid container direction='column'>
			<div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '10px' }}>
				<Link to='/product/add' style={{ textDecoration: 'none', color: 'inherit' }}>
					<Fab size='medium' color='primary' aria-label='Add'>
						<AddIcon />
					</Fab>
				</Link>
			</div>
			{loadingList ? (
				<LoadinCircle generalCenter />
			) : (
				dataList && (
					<CardProductHorizontal
						listElement={dataList}
						handleEditElement={value => props.handleEditElement(value)}
						handleDeleteElement={value => props.handleDeleteElement(value)}
					/>
					// <Pagination count={10} color="primary" page={page} onChange={handleChange}/>
				)
			)}
		</Grid>
	);
}

function AdminBrandList(props) {
	const { loadingListBrand, listBrand } = props.dataProps;
	const [openModal, setOpenModal] = React.useState(false);
	const [editBrand, setEditBrand] = React.useState({});

	React.useEffect(() => {
		if (!openModal && editBrand.name) {
			setEditBrand({});
		}
	}, [openModal]);

	return (
		<Grid container direction='column'>
			<div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '10px' }}>
				<Fab
					size='medium'
					color='primary'
					aria-label='Add'
					onClick={() => {
						setEditBrand({});
						setOpenModal(!openModal);
					}}
				>
					<AddIcon />
				</Fab>
			</div>
			<FormDialog
				brandValue={editBrand}
				open={openModal}
				updateOpenModal={val => {
					setOpenModal(val);
				}}
				dataForm={val => {
					props.handleAddBrand(val);
				}}
				editForm={val => {
					props.handleEditBrand(val);
				}}
			/>
			<Grid container direction='row'>
				{loadingListBrand ? (
					<LoadinCircle generalCenter />
				) : (
					listBrand &&
					listBrand.brandResult.map(item => {
						return (
							<Grid item key={item._id} xs={12} sm={6}>
								<Card key={item._id} style={{ margin: '0px 10px 10px' }}>
									<CardContent>
										<Grid container direction='row' justify='space-between'>
											<Typography>{item.name}</Typography>
											<div>
												<IconButton
													aria-label='edit'
													onClick={() => {
														setOpenModal(!openModal);
														setEditBrand(item);
													}}
												>
													<EditIcon fontSize='small' />
												</IconButton>
												<IconButton
													aria-label='delete'
													onClick={() => {
														props.handleDeleteBrand(item._id);
													}}
												>
													<DeleteIcon fontSize='small' />
												</IconButton>
											</div>
										</Grid>
										<Typography>number of models {item.models.length}</Typography>
									</CardContent>
								</Card>
							</Grid>
						);
					})
				)}
			</Grid>
		</Grid>
	);
}

const DialogTitleCustom = props => {
	const { title, classes, onClose, ...other } = props;
	return (
		<DialogTitle
			disableTypography
			style={{
				display: 'flex',
				flexDirection: 'row',
				alignItems: 'center',
				justifyContent: 'space-between'
			}}
		>
			<Typography variant='h6'>{title}</Typography>
			{onClose ? (
				<IconButton aria-label='close' onClick={onClose}>
					<CloseIcon />
				</IconButton>
			) : null}
		</DialogTitle>
	);
};

function AdminUserList(props) {
	const { loadingListUser, listUser, loadingRegister } = props.dataProps;
	const [open, setOpen] = React.useState(false);
	const [sendingRegister, setSendingRegister] = React.useState(false);
	const [dataToEdit, setToEdit] = React.useState(null);

	React.useEffect(() => {
		if (loadingRegister) {
			setSendingRegister(true);
		} else if (!loadingRegister && sendingRegister) {
			setSendingRegister(false);
			setOpen(false);
			props.handleListUsers();
		}
	}, [loadingRegister]);

	const editUser = data => {
		setToEdit(data);
		setOpen(true);
		//props.handleEditUser(data)
	};
	const handleSubmitUser = data => {
		setOpen(false);
		setToEdit(null);
		if (dataToEdit) {
			props.handleAddUser({ ...dataToEdit, ...data }, 'edit');
		} else {
			props.handleAddUser(data);
		}
	};

	return (
		<React.Fragment>
			<div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '10px' }}>
				<Fab size='medium' color='primary' aria-label='Add' onClick={() => setOpen(!open)}>
					<AddIcon />
				</Fab>
			</div>
			<Dialog open={open} maxWidth={'lg'}>
				<DialogTitleCustom
					title={'Create User'}
					onClose={() => {
						setOpen(false);
						setToEdit(null);
					}}
				/>
				<DialogContent>
					<AuthRegister
						onlyForm
						dataToEdit={dataToEdit}
						onSubmit={data => handleSubmitUser(data)}
					/>
				</DialogContent>
			</Dialog>

			{loadingListUser ? (
				<p>LoadingCircle</p>
			) : (
				listUser && (
					<UserCard
						listUser={listUser.usersList}
						handleDeleteElement={id => props.handleDeleteUser(id)}
						handleEditElement={data => editUser(data)}
					/>
				)
			)}
		</React.Fragment>
	);
}

function AdminProfile(props) {
	return <p>profile Component</p>;
}
function AdminMainPanel(props) {
	return <p>mainPane Component</p>;
}
