import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MaterialTable from 'material-table';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Icon } from '@material-ui/core';

export default function FormDialog(props) {
	const { brandValue } = props;
	const [name, setName] = React.useState('');
	const [models, setModels] = React.useState([]);
	const [toDelete, setToDelete] = React.useState([]);
	const modelStructure = {
		columns: [{ title: 'Name', field: 'name' }]
	};

	React.useEffect(() => {
		setName(brandValue.name ? brandValue.name : '');
		setModels(brandValue.models ? brandValue.models : []);
	}, [brandValue]);

	const handleClose = () => {
		props.updateOpenModal(false);
	};

	const handleSave = () => {
		if (brandValue.name) {
			props.editForm({ _id: brandValue._id, name, models: [...models, ...toDelete] });
		} else {
			props.dataForm({ name, models });
		}
		props.updateOpenModal(false);
	};

	return (
		<Dialog open={props.open} onClose={handleClose} maxWidth={'lg'}>
			<DialogTitle id='form-dialog-title'>Create Brand</DialogTitle>
			<DialogContent>
				<TextField
					autoFocus
					onChange={event => {
						setName(event.target.value);
					}}
					value={name}
					margin='dense'
					id='name'
					label='Brand Name'
					fullWidth
				/>
				<MaterialTable
					title='Models'
					columns={modelStructure.columns}
					data={models}
					localization={{ body: { editRow: { deleteText: 'Are you sure delete this model?' } } }}
					options={{
						search: false,
						actionsColumnIndex: -1
					}}
					editable={{
						onRowAdd: newData =>
							new Promise((resolve, reject) => {
								setTimeout(async () => {
									{
										const data = [...models];
										data.push(newData);
										await setModels(data);
										resolve();
									}
									resolve();
								}, 1000);
							}),
						onRowUpdate: (newData, oldData) =>
							new Promise((resolve, reject) => {
								setTimeout(async () => {
									{
										const data = [...models];
										const index = data.indexOf(oldData);
										data[index] = newData;
										await setModels(data);
										resolve();
									}
									resolve();
								}, 1000);
							}),
						onRowDelete: oldData =>
							new Promise((resolve, reject) => {
								setTimeout(async () => {
									{
										const data = [...models];
										const index = data.indexOf(oldData);
										if (oldData._id) {
											oldData.isDeleted = true;
											let copyDelete = [...toDelete];
											copyDelete.push(oldData);
											setToDelete(copyDelete);
										}
										data.splice(index, 1);
										await setModels(data);
										resolve();
									}
									resolve();
								}, 1000);
							})
					}}
				/>
			</DialogContent>
			<DialogActions>
				<Button onClick={handleClose} color='primary'>
					Cancel
				</Button>
				<Button onClick={handleSave} color='primary'>
					Save
				</Button>
			</DialogActions>
		</Dialog>
	);
}
