import React from 'react';
import {
	Hidden,
	CardMedia,
	withStyles,
	Grid,
	CardContent,
	Typography,
	Card
} from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import CardActions from '@material-ui/core/CardActions';
import AspectRatioIcon from '@material-ui/icons/AspectRatio';
import BatteryStdIcon from '@material-ui/icons/BatteryStd';
import CameraIcon from '@material-ui/icons/Camera';
import StorageIcon from '@material-ui/icons/Storage';
import MemoryIcon from '@material-ui/icons/Memory';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

const styles = theme => ({
	cardGrid: {},
	card: {
		display: 'flex',
		border: '2px solid #e0e0e0',
		borderRadius: '8px',
		justifyContent: 'center',
		alignItems: 'center'
	},
	cardDetails: {
		flex: 1,
		'& > div, & > div:last-child': {
			padding: '8px'
		}
	},
	itemProperty: {
		flexDirection: 'row',
		justifyContent: 'start',
		alignItems: 'center',
		'& > p': {
			margin: '0px',
			marginLeft: '8px'
		}
	},
	cardMedia: {
		width: '200px',
		height: '200px',
		backgroundRepeat: 'no-repeat',
		backgroundSize: 'cover',
		backgroundPosition: 'center'
	},
	controls: {
		display: 'flex',
		justifyContent: 'flex-end'
	}
});

function CardProduct(props) {
	const { classes, listElement, horizontal } = props;

	const mergeUrl = produc => {
		return `${process.env.REACT_APP_API_HOST}` + produc.image;
	};
	return (
		<Grid container spacing={2} className={classes.cardGrid}>
			{listElement.productResult.map(produc => (
				<Grid item key={produc.brand} xs={12} md={6}>
					<Card className={classes.card}>
						<CardMedia className={classes.cardMedia} image={mergeUrl(produc)} title='Image title' />
						<div className={classes.cardDetails}>
							<CardContent>
								<Grid container direction='row' justify='space-between' alignItems='center'>
									<Typography component='h2' variant='h5'>
										{produc.brand.name}
									</Typography>
									<Typography variant='subtitle1' color='textSecondary'>
										${produc.price}
									</Typography>
								</Grid>
								<Typography variant='subtitle1' color='textSecondary'>
									{produc.model.name}
								</Typography>
								<Grid container>
									<Grid item container className={classes.itemProperty} sm={6} md={4}>
										<AspectRatioIcon />
										<Typography variant='subtitle1' paragraph>
											{produc.screen.$numberDecimal}"
										</Typography>
									</Grid>
									<Grid item container className={classes.itemProperty} sm={6} md={4}>
										<CameraIcon />
										<Typography variant='subtitle1' paragraph>
											{produc.camera} mpx
										</Typography>
									</Grid>
									<Grid item container className={classes.itemProperty} sm={6} md={4}>
										<StorageIcon />
										<Typography variant='subtitle1' paragraph>
											{produc.storage} GB
										</Typography>
									</Grid>
									<Grid item container className={classes.itemProperty} sm={6} md={4}>
										<MemoryIcon />
										<Typography variant='subtitle1' paragraph>
											{produc.memory} GB
										</Typography>
									</Grid>
									<Grid item container className={classes.itemProperty} sm={6} md={6}>
										<BatteryStdIcon />
										<Typography variant='subtitle1' paragraph>
											{produc.battery} mAh
										</Typography>
									</Grid>
								</Grid>
							</CardContent>
							<div className={classes.controls}>
								<IconButton
									onClick={() => {
										props.handleEditElement(produc._id);
									}}
								>
									<EditIcon />
								</IconButton>
								<IconButton
									onClick={() => {
										props.handleDeleteElement(produc._id);
									}}
								>
									<DeleteIcon />
								</IconButton>
							</div>
						</div>
					</Card>
				</Grid>
			))}
		</Grid>
	);
}
export default withStyles(styles)(CardProduct);
